import { RaglanPattern, getRaglanPattern } from "../../patternMakers/raglan";
import {
  Size,
  FOSizing,
  baseFOSizing,
  RaglanCorePart,
  raglanCoreParts,
  RaglanMeasurement,
  FOPart,
  baseBodySizing,
  BodyPart,
} from "../../utils/sizes";
import { IntroSizingInfo } from "../../utils/types";
import profile from "./profile";

export const sizes: Size[] = [
  "2XS",
  "XS",
  "S",
  "S+",
  "M",
  "M+",
  "L",
  "L+",
  "XL",
  "2XL",
  "3XL",
  "4XL",
];

// Base pattern calculation
export const sizing = baseBodySizing;

const baseMeasurement: FOSizing = {
  ...baseFOSizing,
  M: {
    ...baseFOSizing.M,
    underarm: 2.125,
    neck_circumference: 18.2,
    bust_circumference: 40.15,
    under_arm_circumference: 14.3,
    yoke_length: 9,
    neck_depth: 2.25,
    total_length: 25,
    wrist_circumference: 9.75,
    sleeve_length_from_underarm: 18.75,
    neck_band_height: 1.125,
    hem: 2.5,
  },
};

export const measurements: Partial<Record<FOPart, number>>[] = sizes.map(
  (size) => {
    const ms = baseMeasurement[size] ?? {};
    return Object.keys(ms)
      .filter((key) => raglanCoreParts.includes(key as RaglanCorePart))
      .reduce((cur, key) => {
        return Object.assign(cur, { [key]: ms?.[key as RaglanCorePart] });
      }, {});
  }
);

export const schemaBySize: RaglanPattern[] = measurements.map((m, i) => {
  return getRaglanPattern(
    profile.gauge.sts_count,
    profile.gauge.row_count,
    m as RaglanMeasurement,
    [2, 1],
    2,
    1,
    1.1
  );
});

const introInfo: IntroSizingInfo = {
  totalSts: schemaBySize.map((schema) => schema.totalSts),
  bodyParts: ["bust"].map((d) => {
    const part = d as BodyPart;
    return {
      part,
      sizes: sizes.map((size) => sizing?.[size as Size]?.[part] ?? 0),
    };
  }),
  foParts: [
    "bust_circumference",
    "neck_circumference",
    "under_arm_circumference",
    "sleeve_length_from_underarm",
    "total_length",
  ].map((d) => {
    const part = d as FOPart;
    return {
      part,
      sizes: sizes.map((size) => baseMeasurement?.[size as Size]?.[part] ?? 0),
    };
  }),
  baseSizing: { size: "M", grams: 499 },
  sizes,
};

export default introInfo;
